@import url(https://fonts.googleapis.com/css2?family=Share+Tech&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Share Tech', sans-serif;
  background: url(/weather/static/media/background-phones.9085f139.jpg) no-repeat center top fixed;
  background-position-x: right;
}


.app {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search {
  margin-top: 3rem;
  padding: 15px 15px;
  font-size: 1.3rem;
  border-radius: 35px;
  width: 64vw;
  max-width: 400px;
  border: none;
}

.weather-card {
  -webkit-filter: opacity(80%);
          filter: opacity(80%);
  font-size: 1.5rem;
  margin-top: 1rem;
  padding: 60px 18vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: #cccccc;
  border-radius: 25px;

  box-shadow: 5px 10px 20px -5px;
}

.weather-card h1 {
  margin-top: 0.5rem;
}

@media only screen and (min-width: 768px) {
  body {
    background: url(/weather/static/media/background-dekstop.895912fc.jpg) no-repeat center center fixed;
    background-size: cover;
  }

  .weather-card {
    padding: 8vh 10vw;
  }
}

